<template>
    <div class="main-content">
        <el-row :gutter="24">
            
            
            <el-col :span="24">
              <el-card class="box-card"  shadow="never">
                <div slot="header" class="clearfix">
                  <span>计划报表</span>
                </div>
                
                <div class="text summary summary-title">
                    
                    <div>花费</div>
                    <div>点击量</div>
                    <div>展现量</div>
                    <div>点击率</div>
                    <div>平均点击花费</div>
                    <div>总成交笔数</div>
                    <div>总成交金额</div>
                    
                </div>
                <div class="text summary">
                    
                    <div>
                        <span  class="rtrpt-num">{{rtrpt.cost}}</span>
                        <span  class="rtrpt-num2">昨：{{rtrpt2.cost}}</span>
                    </div>
                    <div>
                        <span  class="rtrpt-num">{{rtrpt.click}}</span>
                        <span  class="rtrpt-num2">{{rtrpt2.click}}</span>
                    </div>
                    <div>
                        <span  class="rtrpt-num">{{rtrpt.impression}}</span>
                        <span  class="rtrpt-num2">{{rtrpt2.impression}}</span>
                    </div>
                    <div>
                        <span  class="rtrpt-num">{{rtrpt.ctr}}</span>
                        <span  class="rtrpt-num2">{{rtrpt2.ctr}}</span>
                    </div>
                    <div>
                        <span  class="rtrpt-num">{{rtrpt.cpc}}</span>
                        <span  class="rtrpt-num2">{{rtrpt2.cpc}}</span>
                    </div>
                    <div>
                        <span  class="rtrpt-num">{{rtrpt.transactionshippingtotal}}</span>
                        <span  class="rtrpt-num2">{{rtrpt2.transactionshippingtotal}}</span>
                    </div>
                    <div>
                        <span  class="rtrpt-num">{{(rtrpt.transactiontotal/100).toFixed(0)}}</span>
                        <span  class="rtrpt-num2">{{(rtrpt2.transactiontotal/100).toFixed(0)}}</span>
                    </div>

                </div>
                
              </el-card>
            </el-col>
            
            
            
            <el-col :span="24">
              <el-card class="box-card"  shadow="never">
                <div slot="header" class="clearfix">
                  <span>已有计划</span>
                </div>
                <div>
                    <el-button type="primary" @click="newCampaign"> 新建计划 </el-button> 
                    <el-button @click="SetAutoCampaign"> 设定为自动优化 </el-button> 
                    <el-button @click="SetSandardCampaign"> 设定标准推广方案 </el-button>
                </div>
                <div class="text item campains-list" v-loading="loading">
                    
                    <div v-for="item in CampaignsList" :key="item.campaign_id">
                        
                        <el-row :gutter="20">
                            <el-col :span="14">
                                <div class="item_title"><input type="checkbox" v-model="campaignChecked" :value="item.campaign_id"> <span  @click="viewCampaign(item)">{{item.title}}</span> </div>
                            </el-col>
                            <el-col :span="10">
                                <div class="item_title"> {{item.auto=='1'? '自动优化中':''}} </div>
                            </el-col>
                            
                        </el-row>
                        <!-- {{CampaignsList}} -->
                        
                    </div>
                </div>
                
              </el-card>
            </el-col>
            

        </el-row>   
    </div>
     
</template>

<script>
    import {request} from "@/network/request.js";
    
    export default {
        name: 'auto',
        data() {
            return {
                CampaignsList:[],
                show:true,
                campaignChecked:[], //勾选中的计划
                rtrpt:{},//账户实时报表
                rtrpt2:{},//账户昨天报表
                loading:false,                
    
            }
        },
        components:{
        },
        
        methods:{
            handleChooseGoods()
            {
                this.$refs.DrawerChooseGoodsList.drawer = true;
            },
            CreateCampaign()
            {
                
                request({
                    url: "/auto/CampaignAdd",
                    params:{
                        title:this.form.title,
                    },
                    method: 'get',
                }).then(res => {
                    console.log(res)
                    //this.data = res.data.data.items.item;
                    
                    this.$message({
                              message: res.data.data.msg + res.data.data.sub_msg,
                              type: 'success'
                            });
                })
                
            },
            //新建推广计划
            newCampaign()
            {
                this.$router.push({path:'/auto/viewCampaigns'})
            },
            viewCampaign(campaign)
            {
                this.$router.push({path:'/auto/viewCampaigns',query:campaign})
            },
            //taobao.simba.rpt.custeffect.get 用户账户报表效果数据查询
            RtrptCustGet()
            {
                
                request({
                    url: "/auto/RtrptCustGet",
                    params:{
                        "the_date":"2020-06-05"
                    }
                }).then(res => {
                    console.log(res)
                    this.rtrpt = res.data[0];
                    this.rtrpt2 = res.data[1];
                })
                
            },
            
            //设置自动优化
            SetAutoCampaign()
            {
                this.$confirm('是否继续批量设置自动优化?', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                            
                            request({
                                url: "/auto/SetAutoCampaign",
                                params:{
                                    campaignChecked:this.campaignChecked,
                                }
                            }).then(res => {
                                console.log(res)
                                
                                this.$message({
                                  type: 'success',
                                  message: '操作成功!'
                                });
                        })

                })

            },
            
            //设置标准推广方案
            SetSandardCampaign()
            {
                this.$confirm('是否继续批量设置标准推广方案?', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                            
                            
                            this.campaignChecked.forEach((item)=>{
                                
                                request({
                                    url: "/auto/InitCampaign()",
                                    params:{
                                        campaign_id:item,
                                    }
                                }).then(res => {
                                    console.log(res)
                                    
                                    this.$message({
                                      type: 'success',
                                      message: '操作成功!'
                                    });
                            })
                            
                            
                        })
            
                })
            
            }
            
        },
        mounted()
        {
            this.RtrptCustGet()
            this.loading=true;
            
            request({
                url: "/auto/GetCampaigns",
                method: 'get',
            }).then(res => {
                console.log(res)
                this.CampaignsList = res.data;
                this.loading=false;
                
            })
        }
        
    
    
    }
</script>

